@font-face {
    src: url(../fonts/ZillaSlab-Regular.ttf);
    font-family: font;
}

.home {
    background-color:#303030;
    padding-left: 15%;
    padding-top: 5%;
    padding-bottom: 5%;

    background-image: url("../images/about.png");
    background-repeat: no-repeat;
    background-position: left;
    max-width: 100%;
    height: 100%;
    overflow-x: hidden;
}

.major {
    display: flex;
    align-items: center;
    justify-content: left;
}

.title {
    font-family: font;
    color: white;
    font-size: 250%;
    font-weight: 550;
    letter-spacing: 3px;
}

.text {
    font-family: font;
    color: white;
    font-size: 150%;
    font-weight: 550;
    letter-spacing: 1.5px;
    padding-bottom: 3%;
}

.right{
    float: right;
    padding-right: 5%;
}
